import {Component} from 'react';
import './Card.css';
import 'tachyons';

const Card=({obj})=>
{
	let source=`https://robohash.org/${obj.id}? 200x200&set=set${obj.imgType}`;
	if(obj.special_img===true)
		source='data:image/jpeg;base64,';
	return(
		<div className="br3 cardStyle">
			<img src={source} alt="image"/>
			<p>{obj.name}</p>
			
		</div>
	    )
}

export default Card;